import React, {Component} from 'react';
import Calendar from 'react-calendar';
import './CalendarPickerWidget.css'
import axios from "axios";
import moment from "moment";

class CalendarPicker extends Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.state = {
            value: new Date(),
            datesWithEvents: []
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.todaySelected !== this.props.todaySelected)
        {
            this.handleChange(this.props.todaySelected);
        }
    }

    componentDidMount() {
        this.fetchDates();
    }

    handleChange(value) {
        this.props.onDateChange(value);
        this.setState({value});
        this.fetchDates();
    }

    fetchDates() {
        const URL = 'api/all/daysWithEvents';
        console.log(URL);
        axios.get(URL)
            .then(response => {
                this.setState({
                    datesWithEvents: response.data,
                });
            })
            .catch(e => {
                console.log(e);
            });
    }

    render() {
        const value = this.state.value;

        return (
            <Calendar
                className={'calendarPicker'}
                tileContent={this.addStylingToDate()}
                onChange={this.handleChange}
                value={value}
            />
        );
    }

    addStylingToDate() {
        return ({activeStartDate, date, view}) => {
            return view === 'month' && this.dateHasEvents(date) ? "âš«" : null;
        };
    }

    dateHasEvents(date) {
        let containsEvents = false;
        if (Array.isArray(this.state.datesWithEvents) && this.state.datesWithEvents.length > 0) {
            for (let i = 0; i < this.state.datesWithEvents.length; i++) {
                if (moment(this.state.datesWithEvents[i]).isSame(date, "day")) {
                    return true;
                }
            }
        }
        return containsEvents;
    }
}

export default CalendarPicker;
