import React from "react";
import Notice from "./Notice";

class NoticeList extends React.Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange = () => {
        this.props.updated();
    }

    displayNotices() {
        if (!Array.isArray(this.props.notices) || !this.props.notices.length) {
            return "No notices to display.";
        } else {
            return this.props.notices.map((e) =>
                <Notice
                    key={e.id}
                    id={e.id}
                    noticeName={e.noticeName}
                    updated={this.handleChange}
                    hasDeleteButton={this.props.hasDeleteButton}
                />);
        }
    }

    render() {
        let padding;
        if (!this.props.hasDeleteButton) {
            padding = {padding: "60px"};
        }
        return (
            <div style={padding}>
                {this.displayNotices()}
            </div>
        );
    }
}

export default NoticeList;
