import React from "react";
import axios from "axios";
import moment from 'moment';
import Button from "react-bootstrap/Button";

class Event extends React.Component
{
    constructor(props) {
        super(props);
        this.deleteEvent = this.deleteEvent.bind(this);
    }

    deleteEvent = () => {
        const URL = '/api/events/deleteByEventId/' + this.props.id;
        console.log(URL);
        axios.delete(URL)
            .catch(e => {
                console.log(e);
            })
            .finally(this.props.updated());
    }

    styleDate(date){
        return (
            <span>
                <span style={{fontWeight: "bold", color: "#084498"}}>{moment(date).format("HH:mm")}</span>
                {moment(date).format(" dddd, Do MMM YYYY")}
            </span>
        );
    }

    render() {
        let description;
        if (this.props.eventDescription) {
            description = <div>Description: {this.props.eventDescription}</div>;
        }

        let location;
        if (this.props.eventLocation) {
            location = <div>Location: {this.props.eventLocation}</div>;
        }

        let deleteButton;
        if (this.props.hasDeleteButton) {
            deleteButton =
                <Button variant="danger"
                        onClick={this.deleteEvent}
                        style={{float: "right"}}
                >
                    Delete
                </Button>
        }

        let divStyle;
        if (this.props.hasDeleteButton) {
            divStyle = {
                backgroundColor: "#d4d4d9",
                width: "available",
                border: "thin solid #7889FF",
                minHeight: "60px"
            };
        } else {
            divStyle = {
                display: "inline-block",
                backgroundColor: "#d4d4d9",
                border: "thin solid #7889FF",
                minHeight: "60px"
            };
        }
        return (
            <div style={divStyle}>
                <div style={{fontWeight: "bold", fontSize: "22px"}}>
                    {this.props.eventName}
                    {deleteButton}
                </div>
                {description}
                {location}
                <div>From: {this.styleDate(this.props.eventStartTime)}</div>
                <div>To: {this.styleDate(this.props.eventEndTime)}</div>
            </div>
        );
    }
}

export default Event;
