import React from "react";
import ReactNotification, {store} from 'react-notifications-component';
import 'react-notifications-component/dist/theme.css'
import 'animate.css';
import axios from "axios";
import moment from "moment";

class NotificationHandler extends React.Component {
    constructor(props) {
        super(props);
        this.fetchDates();
        this.audio = new Audio("../../public/soundAlert.wav");
    }

    componentDidMount() {
        setInterval(() => this.fetchDates(), 60 * 1000);
    }

    componentWillUnmount() {
        clearInterval(this.interval);
    }

    fetchDates() {
        const URL = 'api/events/nextMinutes/60';
        axios.get(URL)
            .then(response => {
                const now = moment(new Date()).startOf('minute');
                response.data.map((e) => {
                    const eventTime = moment(e.startTime).startOf('minute');
                    const minutesUntilEvent = eventTime.diff(now, 'minutes');
                    if ([1,5,10,15,30,60].includes(minutesUntilEvent)) {
                        this.createNotification(e.eventName, e.startTime, minutesUntilEvent);
                    }
                })
            })
            .catch(e => {
                console.log(e);
            });
    }

    createNotification = (name, date, mins) => {
        store.addNotification({
            title: name,
            message: 'Event starts at ' + moment(date).format("HH:mm") + ', in ' + mins + ' minutes',
            type: 'warning',                         // 'default', 'success', 'info', 'warning'
            container: 'bottom-right',                // where to position the notifications
            animationIn: ["animated", "fadeIn"],     // animate.css classes that's applied
            animationOut: ["animated", "fadeOut"],   // animate.css classes that's applied
            // dismiss: {
            //     duration: 10000,
            //     onScreen: true
            // }
        });
        this.audio.play();
    }

    render() {
        return (
            <ReactNotification/>
        );
    }
}

export default NotificationHandler;
