import React, {Component} from "react";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import ReactToPrint from "react-to-print";
import axios from "axios";
import EventList from "../calendar/event/EventList";

class ListModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            show: false,
            events: [],
            noOfDays: 1,
            timePeriod: 'day'
        };
        this.convertPeriodToDays = this.convertPeriodToDays.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.fetchEvents = this.fetchEvents.bind(this);
        this.updatedEvent = this.updatedEvent.bind(this);
    }

    handleShow = () => {
        this.setState({show: true});
        this.fetchEvents();
    };

    handleChange(event) {
        this.setState({timePeriod: event.target.value});
    };

    handleClose = () => {
        this.setState({
            show: false,
            events: [],
            noOfDays: 1,
            timePeriod: 'day'
        });
    };

    updatedEvent = () => {
        setTimeout(() => {this.fetchEvents()}, 150);
    }

    fetchEvents() {
        const timePeriod = this.convertPeriodToDays();
        const URL = '/api/events/period/' + timePeriod;
        console.log(URL);
        axios.get(URL)
            .then(response => this.setState({
                events: response.data,
            }))
            .catch(e => {
                console.log(e);
            });
    }

    convertPeriodToDays = () => {
        let timePeriod = this.state.noOfDays;
        let unitOfMeasure = this.state.timePeriod;

        let multiplier;
        switch(unitOfMeasure)
        {
            case "year": multiplier = 365; break;
            case "month": multiplier = 30; break;
            case "week": multiplier = 7; break;
            case "day": multiplier = 1; break;
            default: multiplier = 1;
        }

        return timePeriod * multiplier;
    }

    render() {
        return (
            <>
                <Button variant="primary" onClick={this.handleShow}>
                    List Events
                </Button>

                <Modal size="lg"
                       show={this.state.show}
                       onHide={this.handleClose}
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Upcoming events in:</Modal.Title>
                        <input type="number"
                               style={{height: "38px", marginLeft: "5px", width: "50px"}}
                               defaultValue={1}
                               onChange={event => this.setState({noOfDays: event.target.value.replace(/\D/,'')})}
                        />
                        <label>
                            <select value={this.state.timePeriod}
                                    onChange={this.handleChange}
                                    style={{height: "38px", marginLeft: "5px", marginRight: "5px"}}
                            >
                                <option value="day">day</option>
                                <option value="week">week</option>
                                <option value="month">month</option>
                                <option value="year">year</option>
                            </select>
                        </label>
                        <Button variant="primary" onClick={this.fetchEvents}>
                            List
                        </Button>
                    </Modal.Header>
                    <Modal.Body>
                        <EventList events={this.state.events}
                                   updated={this.updatedEvent}
                                   hasDeleteButton={true}
                        />
                    </Modal.Body>
                    <Modal.Footer>
                        <ReactToPrint
                            trigger={() => <Button variant="primary">Print</Button>}
                            content={() => this.componentRef}
                        />
                        <div style={{ display: "none"}}>
                            <EventList
                                events={this.state.events}
                                updated={this.updatedEvent}
                                hasDeleteButton={false}
                                ref={el => (this.componentRef = el)}
                            />
                        </div>
                        <Button variant="secondary" onClick={this.handleClose}>
                            Close
                        </Button>
                    </Modal.Footer>
                </Modal>
            </>
        );
    }
}

export default ListModal;
