import React from "react";
import Event from "./Event";

class EventList extends React.Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange = () => {
        this.props.updated();
    }

    displayEvents() {
        if (!Array.isArray(this.props.events) || !this.props.events.length) {
            return "No events to display.";
        } else {
            return this.props.events.map((e) =>
                <Event
                    key={e.id}
                    id={e.id}
                    eventName={e.eventName}
                    eventDescription={e.description}
                    eventLocation={e.location}
                    eventStartTime={e.startTime}
                    eventEndTime={e.endTime}
                    updated={this.handleChange}
                    hasDeleteButton={this.props.hasDeleteButton}
                />);
        }
    }

    render() {
        let padding;
        if (!this.props.hasDeleteButton) {
            padding = {padding: "60px"};
        }
        return (
            <div style={padding}>
                {this.displayEvents()}
            </div>
        )
    }
}

export default EventList;
