import React from "react";
import axios from "axios";
import EasyEdit from "react-easy-edit";

class Notice extends React.Component {
    constructor(props) {
        super(props);
        this.deleteNotice = this.deleteNotice.bind(this);
    }

    editNotice = (modifiedText) => {
        if (modifiedText === '') {
            this.deleteNotice();
            return;
        }
        const updatedNotice = {
            id: this.props.id,
            noticeName: modifiedText
        };
        const URL = '/api/notices/edit/';
        console.log(URL);
        axios.put(URL, updatedNotice)
            .then(res => console.log(res))
            .catch(e => {
                console.log(e);
            })
            .finally(this.props.updated());
    }

    deleteNotice = () => {
        const URL = '/api/notices/deleteByNoticeId/' + this.props.id;
        console.log(URL);
        axios.delete(URL)
            .catch(e => {
                console.log(e);
            })
            .finally(this.props.updated());
    }

    render() {
        let divStyle;
        if (this.props.hasDeleteButton) {
            divStyle = {
                backgroundColor: "#d4d4d9",
                width: "available",
                border: "thin solid #7889FF",
                minHeight: "35px",
                fontWeight: "bold",
                fontSize: "25px"
            };
        } else {
            divStyle = {
                display: "inline-block",
                backgroundColor: "#d4d4d9",
                border: "thin solid #7889FF",
                minHeight: "35px",
                fontWeight: "bold",
                fontSize: "25px"
            };
        }

        return (
            <div style={divStyle}>
                <EasyEdit
                    type="textarea"
                    onSave={this.editNotice}
                    value={this.props.noticeName}
                    saveButtonLabel="Save"
                    placeholder={""}
                />
            </div>
        );
    }
}

export default Notice;
