import React from 'react';
import {Day, DragAndDrop, Inject, Resize, ScheduleComponent} from "@syncfusion/ej2-react-schedule";
import {DataManager} from "@syncfusion/ej2-data";
import CustomAdaptor from "./CustomAdaptor";
import './HourlyGrid.css'

class HourlyGrid extends React.Component {
    constructor(props) {
        super(props);
        this.dataManager = new DataManager({
            url: '/api/events/calendar',
            adaptor: new CustomAdaptor(),
            batchUrl: '/$batch',
            // crudUrl: '',
            insertUrl: 'events/calendar/add',
            updateUrl: 'events/calendar/edit',
            removeUrl: 'events/calendar/delete',
            requestType: ''
        });
        setInterval(() => {
            this.forceUpdate();
        }, 2000)
    }

    render() {
        let whatDay = this.props.selectedDay;
        return (
            <ScheduleComponent
                style={{backgroundColor: 'lightgray'}}
                currentView='day'
                showHeaderBar={false}
                height='100%'
                eventSettings={{dataSource: this.dataManager}}
                selectedDate={whatDay}
                startHour='09:00'
            >
                <Inject services={[Day, Resize, DragAndDrop]}/>
            </ScheduleComponent>
        )
    }
}

export default HourlyGrid;
