import React from "react";
import axios from "axios";
import NoticeList from "./NoticeList";
import moment from "moment";

class DailyEvents extends React.Component {
    constructor(props) {
        super(props);
        this.state = {notices: [], text: ''};
        this.updatedEvent = this.updatedEvent.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.fetchNotices();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.selectedDay !== this.props.selectedDay) {
            this.fetchNotices();
        }
    }

    updatedEvent = () => {
        setTimeout(() => {this.fetchNotices()}, 150);
    }

    fetchNotices() {
        let selectedDay = this.props.selectedDay;
        const URL = '/api/notices/day/' + moment(selectedDay).format();
        console.log(URL);
        axios.get(URL)
            .then(response => this.setState({
                notices: response.data,
            }))
            .catch(e => {
                console.log(e);
            });
    }

    handleChange(e) {
        this.setState({text: e.target.value});
    }

    handleSubmit(e) {
        e.preventDefault();
        let trimmedValue = this.state.text.trim();
        if (trimmedValue.length === 0) {
            this.setState(state => ({
                ...state,
                text: ''
            }));
            return;
        }
        const newItem = {
            noticeName: this.state.text,
            day: this.props.selectedDay
        };

        axios.post("/api/notices/add", newItem)
            .then(res => console.log(res))
            .then(() => this.fetchNotices())
        this.setState(state => ({
            ...state,
            text: ''
        }));
    }

    recurringEvent = (e) => {
        e.preventDefault();
        console.log(this.state.text);
    }

    render() {
        return (
            <div>
                <div>
                    <form onSubmit={this.handleSubmit} style={{display: "flex"}}>
                        <input
                            style={{width: "75%", flex: "auto"}}
                            id="new-todo"
                            onChange={this.handleChange}
                            value={this.state.text}
                        />
                        <button type="submit">
                            Add
                        </button>
                        <button type="button" onClick={this.recurringEvent}>
                            Repeat
                        </button>
                    </form>
                </div>

                <NoticeList
                    notices={this.state.notices}
                    updated={this.updatedEvent}
                    hasDeleteButton={true}
                />
            </div>
        );
    }
}

export default DailyEvents;
