import React from "react";
import SplitPane from "react-split-pane";
import './SplitPaneStyle.css';
import CalendarPicker from "./dayPicker/CalendarPicker";
import DailyEvents from "./notices/DailyEvents";
import './MainCalendarStyle.css';
import HourlyGridContainer from "./scheduler/HourlyGridContainer";

class MainCalendar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {selectedDay: new Date()}
        this.handleDateChange = this.handleDateChange.bind(this);
    }

    handleDateChange(value) {
        this.setState({
            selectedDay: value
        }, () => console.log(this.state.selectedDay));
    }

    render() {
        const selectedDay = this.state.selectedDay;
        return (
            <div className="mainCalendarWidget">
                <SplitPane split="vertical" minSize={300}>
                    <SplitPane split="horizontal" minSize={388} maxSize={475} pane2Style={{overflowY: 'auto'}}>
                        <CalendarPicker onDateChange={this.handleDateChange} todaySelected={this.props.todaySelected}/>
                        <DailyEvents selectedDay={selectedDay}/>
                    </SplitPane>

                    <HourlyGridContainer selectedDay={selectedDay}/>
                </SplitPane>
            </div>
        );
    }
}

export default MainCalendar;
