import {DataUtil, WebApiAdaptor} from "@syncfusion/ej2-data";

class CustomAdaptor extends WebApiAdaptor {

    /**
     * Returns the data from the query processing. It is checking if there is a response, and then processing the data.
     *
     * @param  {DataResult} data
     * @param  {DataOptions} ds?
     * @param  {Query} query?
     * @param  {XMLHttpRequest} xhr?
     * @param  {Ajax} request?
     * @param  {CrudOptions} changes?
     * @returns aggregateResult
     */
    processResponse(data, ds, query, xhr, request, changes) {
        // calling base class processResponse function
        let original = super.processResponse.apply(this, arguments);
        // do something only if there is a response
        if (original !== "") {
            original.forEach((item) => {
                item['EventID'] = item['id'];
                item['Subject'] = item['eventName'];
                item['StartTime'] = item['startTime'];
                item['EndTime'] = item['endTime'];
                item['Description'] = item['description'];
                item['RecurrenceRule'] = item['recurrenceRule'];
                item['Location'] = item['location'];
                item['IsAllDay'] = item['isAllDay'];
            });
        }
        return original;
    }

    /**
     * Prepare the request body based on the newly added, removed and updated records.
     * The result is used by the batch request.
     * @param  {DataManager} dm
     * @param  {CrudOptions} changes
     * @param  {RemoteArgs} e
     * @returns {Object}
     */
    batchRequest = function (dm, changes, e) {
        let _this = this;
        let url = dm.dataSource.url.replace(/\/*$/, '/' + this.options.batch);
        e.url = this.resourceTableName ? this.resourceTableName : e.url;
        let req = [];
        let _loop_1 = function (i) {
            changes.addedRecords.forEach(function (j) {
                req.push('POST /api/' + (dm.dataSource.insertUrl || dm.dataSource.crudUrl || e.url) + ' HTTP/1.1');
                req.push(j ? JSON.stringify(j) : '');
            });
        };
        //insertion
        for (let i = 0, x = changes.addedRecords.length; i < x; i++) {
            _loop_1(i);
        }
        let _loop_2 = function (i) {
            changes.changedRecords.forEach(function (j, d) {
                let stat = {
                    'method': _this.options.updateType + ' ',
                    'url': function (data, i, key) { return ''; },
                    'data': function (data, i) { return JSON.stringify(data[i]) + '\n\n'; }
                };
                req.push('PUT /api/' + (dm.dataSource.updateUrl || dm.dataSource.crudUrl || e.url)
                    + stat.url(changes.changedRecords, i, e.key) + ' HTTP/1.1');
                req.push(j ? JSON.stringify(j) : '');
            });
        };
        //updating
        for (let i = 0, x = changes.changedRecords.length; i < x; i++) {
            _loop_2(i);
        }
        let _loop_3 = function (i) {
            changes.deletedRecords.forEach(function (j, d) {
                let state = {
                    'mtd': 'DELETE ',
                    'url': function (data, i, key) {
                        let url = DataUtil.getObject(key, data[i]);
                        if (typeof url === 'number' || DataUtil.parse.isGuid(url)) {
                            return '/' + url;
                        }
                        else if (url instanceof Date) {
                            let datTime = data[i][key];
                            return '/' + datTime.toJSON();
                        }
                        else {
                            return "/'" + url + "'";
                        }
                    },
                    'data': function (data, i) { return ''; }
                };
                req.push('DELETE /api/' + (dm.dataSource.removeUrl || dm.dataSource.crudUrl || e.url)
                    + state.url(changes.deletedRecords, i, "id") + ' HTTP/1.1');
                req.push(j ? JSON.stringify(j) : '');
            });
        };
        //deletion
        for (let i = 0, x = changes.deletedRecords.length; i < x; i++) {
            _loop_3(i);
        }
        return {
            type: 'POST',
            url: url,
            contentType: '',
            data: req.join('\r\n')
        };
    };
}

export default CustomAdaptor;
