import React, {Component} from "react";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import axios from "axios";
import EventList from "../calendar/event/EventList";

class SearchButtonModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            show: false,
            events: []
        };
        this.updatedEvent = this.updatedEvent.bind(this);
    }

    handleShow = () => {
        this.fetchEvents();
        this.setState({show: true});
    };

    handleClose = () => {
        this.setState({show: false});
    };

    updatedEvent = () => {
        setTimeout(() => {this.fetchEvents()}, 150);
    }

    fetchEvents() {
        let queryString = this.props.queryString;
        const URL = '/api/events/eventNameContaining/' + queryString;
        console.log(URL);
        axios.get(URL)
            .then(response => this.setState({
                events: response.data,
            }))
            .catch(e => {
                console.log(e);
            });
    }

    render() {
        return (
            <>
                <Button variant="primary" onClick={this.handleShow}>
                    Search
                </Button>

                <Modal show={this.state.show} onHide={this.handleClose}>
                    <Modal.Header closeButton>
                        <Modal.Title>Events containing '{this.props.queryString}'</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <EventList
                            events={this.state.events}
                            updated={this.updatedEvent}
                            hasDeleteButton={true}
                        />
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={this.handleClose}>
                            Close
                        </Button>
                    </Modal.Footer>
                </Modal>
            </>
        );
    }
}

export default SearchButtonModal;
